import React, { useState } from "react";
import { BiSearchAlt2 } from "react-icons/bi";
import OtherUsers from "./OtherUsers";
import axios from "axios";
import toast from "react-hot-toast";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { setAuthUser, setOtherUsers } from "../redux/userSlice";


const Sidebar = () =>{
    const navigate = useNavigate();
    const [search,setSearch] = useState("");
    const {otherUsers} = useSelector(store=>store.user);
    //const searchList = otherUsers;
    const dispatch = useDispatch();

    const logoutHandler = async() =>{
        try {
            const res = await axios.get("/api/v1/user/logout");
            navigate("/login");
            toast.success(res.data.message);
            dispatch(setAuthUser(null));
        } catch (error) {
           console.log(error); 
        }
    }
    const searchSubmitHandler = async (e)=>{
        e.preventDefault();
        const searchedUser = otherUsers?.find((user)=>user.fullName.toLowerCase().includes(search.toLowerCase()));
        if(searchedUser){
            dispatch(setOtherUsers([searchedUser]));
        }
        else{
            toast.error("User not found");  
        }
    }
    return (<>
        <div className='border-r border-slate-500 p-4 flex flex-col'>
            <form onSubmit={(e) => searchSubmitHandler(e)} action="" className='flex items-center gap-2'>
               <input  className='input input-bordered rounded-md'
                       type="text" placeholder='Search...'
                       value = {search}
                       onChange = {(e)=>setSearch(e.target.value)}
                       />
               <button type='submit' className='btn bg-zinc-700 text-white'>
                    <BiSearchAlt2 className='w-6 h-6 outline-none'/>
                </button>
            </form>
            <div className="divider px-3"></div>
            <OtherUsers/>
            <div className="mt-2">
                <button onClick = {logoutHandler} className="btn btn-sm">Logout</button>
            </div>
        </div>
    </>);
}

export default Sidebar;