import React, { useState } from "react";
import { Link, useNavigate} from "react-router-dom";
import axios from "axios";
import toast from "react-hot-toast";

const Signup = () => {
  const [user, setUser] = useState({
    fullName: "",
    username: "",
    password: "",
    confirmPassword: "",
    gender: "",
  });
  
  const navigate = useNavigate();

  const onSubmitHandler = async(e) => {
    e.preventDefault();
    try {
        const res =  await axios.post("/api/v1/user/register",user,{
            headers : {
                'Content-Type' : 'application/json'
            },
            withCredentials : true
         });
         //console.log(res);
         if(res.data.success){
             navigate("/login");
             toast.success(res.data.message);
         }
    } catch (error) {
       toast.error(error.response.data.message);
       console.log(error); 
    }
    setUser({
        fullName: "",
        username: "",
        password: "",
        confirmPassword: "",
        gender: "",
      });
  };

  const handleCheckbox = (gvalue) => {
    setUser({ ...user, gender: gvalue });
  };

  return (
    <div className="min-w-96 mx-auto">
      <div className="w-full p-6 rounded-lg shadow-md bg-gray-400 bg-clip-padding backdrop-filter backdrop-blur-md bg-opacity-10 border border-gray-100">
        <h1 className="text-3xl font-bold text-center text-white drop-shadow-lg">
          SignUp
        </h1>
        <form onSubmit={onSubmitHandler} action="">
          <div>
            <label className="label p-2">
              <span className="text-base label-text text-gray-100">
                Full Name
              </span>
            </label>
            <input
              className="w-full h-10 input input-bordered"
              type="text"
              placeholder="Full Name"
              value={user.fullName}
              onChange={(e) => setUser({ ...user, fullName: e.target.value })}
            />
          </div>
          <div>
            <label className="label p-2">
              <span className="text-base label-text text-gray-300">
                Username
              </span></label>
            <input
              className="w-full h-10 input input-bordered"
              type="text"
              placeholder="Username"
              value={user.username}
              onChange={(e) => setUser({ ...user, username: e.target.value })}/>
          </div>
          <div>
            <label className="label p-2">
              <span className="text-base label-text text-gray-300">
                Password
              </span>
            </label>
            <input
              className="w-full h-10 input input-bordered"
              type="password"
              placeholder="Password"
              value={user.password}
              onChange={(e) => setUser({ ...user, password: e.target.value })}
            />
          </div>
          <div>
            <label className="label p-2">
              <span className="text-base label-text text-gray-300">
                Confirm Password
              </span>
            </label>
            <input
              className="w-full h-10 input input-bordered"
              type="password"
              placeholder="Confirm Password"
              value={user.confirmPassword}
              onChange={(e) =>
                setUser({ ...user, confirmPassword: e.target.value })
              }
            />
          </div>
          <div className="flex items-center my-4">
            <div className="flex items-center">
              <p className="text-gray-100">Male</p>
              <input type="checkbox" 
                     checked = {user.gender === "male"}
                     onChange = {()=>handleCheckbox("male")}
                     className="checkbox mx-2" />
            </div>
            <div className="flex items-center">
              <p className="text-gray-100">Female</p>
              <input type="checkbox"  
                     checked = {user.gender === "female"}
                     onChange = {()=>handleCheckbox("female")}
                     className="checkbox mx-2" />
            </div>
          </div>
          <p className="text-center my-2">
            Already have an account?{" "}
            <Link to="/login" className="text-blue-50">
              {" "}
              login{" "}
            </Link>
          </p>
          <div>
            <button
              type="submit"
              className="btn btn-block btn-sm mt-2 border border-slate-700"
            >
              Singup
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Signup;
